'use client';

import { useState } from 'react';
import { useWallet } from '../app/context/WalletContext';

interface DashboardProfileProps {
  name: string
  walletId: string
  solidPoints: number
  multiplier: number
  avatarUrl: string
}

export function DashboardProfile({ 
  name, 
  walletId, 
  solidPoints, 
  multiplier, 
  avatarUrl 
}: DashboardProfileProps) {
  const { profile, account, updateProfile } = useWallet();
  const [isEditing, setIsEditing] = useState(false);
  const [editName, setEditName] = useState(name);
  const [editAvatarUrl, setEditAvatarUrl] = useState(avatarUrl);

  const handleSaveProfile = async () => {
    await updateProfile(editName, editAvatarUrl);
    setIsEditing(false);
  };

  if (!account) {
    return (
      <div className="rounded-2xl bg-[#060709] p-6">
        <p className="text-center text-gray-400">Please connect your wallet to view profile</p>
      </div>
    );
  }

  if (isEditing) {
    return (
      <div className="rounded-2xl bg-[#060709] p-6">
        <div className="space-y-4">
          <div>
            <label className="block text-sm text-gray-400 mb-1">Name</label>
            <input
              type="text"
              value={editName}
              onChange={(e) => setEditName(e.target.value)}
              className="w-full px-4 py-2 bg-[#1c1d29] rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
          <div>
            <label className="block text-sm text-gray-400 mb-1">Avatar URL</label>
            <input
              type="text"
              value={editAvatarUrl}
              onChange={(e) => setEditAvatarUrl(e.target.value)}
              className="w-full px-4 py-2 bg-[#1c1d29] rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
          <div className="flex gap-2">
            <button
              onClick={handleSaveProfile}
              className="px-4 py-2 bg-blue-500 rounded-lg hover:bg-blue-600 transition-colors"
            >
              Save
            </button>
            <button
              onClick={() => {
                setIsEditing(false);
                setEditName(name);
                setEditAvatarUrl(avatarUrl);
              }}
              className="px-4 py-2 bg-[#1c1d29] rounded-lg hover:bg-[#2a2b3d] transition-colors"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="rounded-2xl bg-[#060709] p-6" id='myClasses'>
      <div className="flex items-start justify-between mb-6">
        <div className="relative">
          <img
            src={avatarUrl}
            alt="Profile"
            className="w-24 h-24 rounded-full"
          />
        </div>
        <button
          onClick={() => setIsEditing(true)}
          className="px-4 py-2 text-sm bg-[#1c1d29] rounded-lg hover:bg-[#2a2b3d] transition-colors"
        >
          Edit profile
        </button>
      </div>
      
      <div className="space-y-4">
        <div>
          <h2 className="text-2xl font-bold">{name}</h2>
          <p className="text-gray-400 flex items-center gap-2">
            {walletId}
            <button
              onClick={() => navigator.clipboard.writeText(walletId)}
              className="hover:text-gray-300"
              title="Copy address"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
              </svg>
            </button>
          </p>
        </div>
        
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2">
            <span className="text-yellow-500">‚òÖ</span>
            <span>Soul Points</span>
            <span className="font-bold">{solidPoints}</span>
          </div>
          <div className="flex items-center gap-2 text-blue-400">
            <span>Multiplier</span>
            <span className="font-bold">{multiplier}x</span>
          </div>
        </div>
      </div>
    </div>
  );
}
  
  